import * as React from "react";
import {
  JSWindow, ListView, SplitView
} from "@jswf/react";

export function ListViewTest() {
    let listViewRef = React.useRef<ListView>(null);
  const [message, setMessage] = React.useState("");
  let count = 1;
  return (
      <JSWindow width={600} title="ListView" >
        <SplitView>
          <div>
            <button
              onClick={() => {
                //
                listViewRef.current!.addItem([
                  count++,
                  <>
                    <br />
                  </>,
                  0,
                  1
                ]);
              }}
            >
              
            </button>
            <br />
            DOM
            <br />
            <br />
            <button
              onClick={() => {
                const items = listViewRef.current!.getSelectItems();
                for (const item of items)
                  listViewRef.current!.setItem(item, 1, "");
              }}
            >
              
            </button>
            <br />
            
            <br />
            <br />
            <button
              onClick={() => {
                const items = listViewRef.current!.getSelectItems();
                items.sort((a, b) => b - a);
                for (const item of items) listViewRef.current!.removeItem(item);
              }}
            >
              
            </button>
            <br />
            
            <br />
            <br />
            {message}
          </div>
          <ListView
            ref={listViewRef}
            onItemClick={(row, col) => {
              //
              const item = listViewRef.current!.getItem(row, col);
              //
              if (item)
                setMessage(`${item.toString()}`);
            }}
            onDrop = {(e)=>{
              const items = JSON.parse(e.dataTransfer.getData("text/plain"));
              for(const item of items)
                listViewRef.current!.addItem( item as React.ReactNode[]);
             // console.log(e.dataTransfer.getData("text/plain"));
            }}
          >
            <div>
              <div data-type="number">No</div>
              <div data-width={100}>
                
                <br />
                
              </div>
              <div data-type="number"></div>
              <div data-type="number"></div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>5</div>
              <div>10</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>10</div>
              <div>40</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>18</div>
              <div>120</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>30</div>
              <div>380</div>
            </div>
            <div>
              <div>{count++}</div>
              <div></div>
              <div>40</div>
              <div>700</div>
            </div>
          </ListView>
        </SplitView>
      </JSWindow>
  );
}
